.homeContainer{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 1080px;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.cropTool{
    max-width: 1080px;
    border: dashed 1px rgb(0, 0, 0);
    background: #00000086;
    z-index: 100;
}

.canvas{
    position: absolute;
    width: 100vw;
    height: 100vh;
    opacity: 0;
}

.controls{
    position: absolute;
    bottom: 35px;
    color: white;
    background-color: black;
    padding: 20px;
    z-index: 100;
}

.captureBtn{
    cursor: pointer;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 50px;
    background-color: white;
    border: none;
}

.captureBtn:hover{
    background-color: rgba(255, 255, 255, 0.76);
}

.fileUploadBtn{
    cursor: pointer;
    padding: 5px 10px 5px 10px;
    margin: 10px;
}

.uploadBtn{
    cursor: pointer;
    padding: 5px 10px 5px 10px;
    margin: 10px;
}